.modal {
  top: 0;
  left: 0;
  position: fixed;
  background: rgba(0,0,0,0.3);
  width: 100%;
  height: 100%;

  &__content {
    width: 400px;
    padding: 20px;
    margin: 100px auto;
    background: white;
    border-radius: 10px;
  }

  &__close {
    width: 400px;
    position: relative;

    &_button {
      text-align: center;
      width: 20px;
      height: 20px;
      padding: 5px;
      border-radius: 50%;
      position: absolute;
      right: 0;

      &:hover {
        background: #5f636828;
        cursor: pointer;
      }
    }
  }
}